@font-face {
    font-family: "Forum";
    src: url("../fonts/Forum.eot") format("eot"),
    url("../fonts/Forum.woff") format("woff"),
    url("../fonts/Forum.ttf") format("opentype");
    font-weight: normal;
    font-style: normal;
    font-display: swap;
}
@font-face {
    font-family: "PlayfairDisplay";
    src: url("../fonts/PlayfairDisplay-Regular.eot") format("eot"),
    url("../fonts/PlayfairDisplay-Regular.woff") format("woff"),
    url("../fonts/PlayfairDisplay-Regular.ttf") format("opentype");
    font-weight: normal;
    font-style: normal;
    font-display: swap;
}
